<template>
  <img alt="Vue logo" src="./assets/logo.png" />
  <div v-if="this.apps['twitter']"><Twitter /></div>
  <div v-if="this.apps['welcome']"><welcome /></div>
  <div v-if="this.apps['selfies']"><selfiesApp /></div>

  <div>
    <button @click="showApp('twitter')">Twitter</button>
    <button @click="showApp('selfies')">Photo album</button>
    <div><button @click="showApp('welcome')">Home page</button></div>
  </div>
</template>

<script>
import vueTwitter from "./components/vueTwitter.vue";
import welcomePage from "./components/welcomePage.vue";
import selfiesApp from "./components/selfiesApp.vue";
export default {
  name: "App",
  components: {
    Twitter: vueTwitter,
    Welcome: welcomePage,
    selfiesApp: selfiesApp,
  },
  data() {
    return {
      apps: { twitter: false, welcome: true, selfies: false },
    };
  },
  methods: {
    showApp(p) {
      for (const name in this.apps) {
        if (name === p) this.apps[name] = true;
        else this.apps[name] = false;
      }
    },
  },
};
</script>

