<template>
  <div id="container">
    <h1>THE VUE VIEW</h1>
    <div id="searchBar">
      <input
        id="searchBox"
        type="text"
        placeholder="Search Tweets"
        v-model="searchQuery"
        @blur="search1"
        @keyup="search1"
        @input="search1"
      />
      <button>Search</button>
    </div>
    <div class="results">
      <h2 v-if="f">Search Results</h2>
      <div class="tweet" v-for="twt in filteredTweets" :key="twt">
        <h3>{{ twt.user }}</h3>
        <p>{{ twt.tweet }}</p>
      </div>
    </div>
    <div class="user">
      <div class="user-information">
        <div class="image-container">
          <img
            src="https://content.codecademy.com/courses/what-is-vue/profile.jpeg"
            alt="profile picture"
          />
        </div>
        <h3>{{ fullTweet.username }}</h3>
      </div>
      <div class="user-bio"><em>Bio: </em> {{ bio }}.</div>
    </div>
    <div class="tweets">
      <h2>Tweets</h2>
      <div v-if="nt" class="tweet">
        <h3 style="width: 100%; text-align: center">No tweets yet.</h3>
      </div>
      <div class="tweet" v-for="tw in tweets" :key="tw">
        <div>
          <h3>{{ tw.user }}</h3>
          <p>{{ tw.tweet }}</p>
        </div>
        <div id="deleteBtn">
          <button @click="deleteTweet">Delete Tweet</button>
        </div>
      </div>
      <!--
      <div class="tweet">
        <h3>{{ fullTweet.username }}</h3>
        <p>{{ fullTweet.tweet }}</p>
      </div>  -->
    </div>
    <div class="settings">
      <h2>Settings</h2>
      <label for="username">Username</label>
      <input type="text" placeholder="Username" v-model="fullTweet.username" />
      <label for="bio">Bio</label>
      <textarea placeholder="Bio" v-model="bio"></textarea>
      <label for="tweet">New Tweet</label>
      <div class="new-tweet">
        <input
          id="tweetBox"
          type="text"
          placeholder="New Tweet"
          v-model="fullTweet.tweet"
          @keyup.enter="addTweet"
        />
        <button @click="addTweet">Add Tweet</button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "vueTwitter",
  data() {
    return {
      fullTweet: {
        username: "CoderInTraining",
        tweet: "",
      },
      tweets: [],
      searchQuery: "",
      bio: "Excited future software engineer.",
      f: false,
      nt: true,
    };
  },
  methods: {
    search1() {
      if (this.searchQuery === "") this.f = false;
      else this.f = true;
    },
    addTweet() {
      //save Tweet
      this.tweets.push({
        user: this.fullTweet.username,
        tweet: this.fullTweet.tweet,
      });
      this.fullTweet.tweet = "";
      if (this.tweets.length > 0) this.nt = false;
    },
    deleteTweet(index) {
      this.tweets.splice(index, 1);
      if (this.tweets.length === 0) this.nt = true;
    },
  },
  computed: {
    filteredTweets() {
      if (this.tweets.length > 0) {
        var query = this.searchQuery;
        var arr = [];
        var valObj = this.tweets.filter(function (elem) {
          if (query !== "" && elem.tweet.includes(query)) {
            arr.push({
              user: elem.user,
              tweet: elem.tweet,
            });
            return elem.user;
          }
        });
        if (valObj.length > 0) {
          return arr;
        }
      }
    },
  },
};
</script>

